import React, { memo, useContext,useCallback } from 'react';
// components
import FormInput from '../FormInput';
import Spinner from '../Spinner';
import { Alert, Button, Form } from 'react-bootstrap';
import classnames from 'classnames';
import { SearchContext } from '../../../../layouts/context/SearchContext';
import { DashboardContext } from '../../../../layouts/context/DashboardContext';

const SearchFechas = () => {
    const {
        itemsmenuprincipal,
        isLoading,
        itemsmenu,
    } = useContext(DashboardContext);
    const {
      itemsForm,
      onItemSedes,
      handleSubmit,
      validated,
      onSelectFecha,
  } = useContext(SearchContext);


    /*
     * handle form submission
     */
    const getYearsInRange=useCallback(() => {
      const years = [];
      const startYear = '2006';
      const endYear = '2023';

      for (let year = startYear; year <= endYear; year++) {
        years.push(year);
      }

      return years;
    }, []);
    const fechas = getYearsInRange();
    return (
        <>
            <Alert variant={'success'}>
                <Form noValidate validated={validated} onSubmit={handleSubmit}>
                    <Form.Group className={classnames('mh-100 d-flex flex-row')}>
                        <Form.Group className={classnames('w-65 p-0')}>
                            <FormInput
                                type="select"
                                label="Sede"
                                name="Sedes"
                                className="form-control"
                                containerClass={'w-65  p-0'}
                                key="Sedes"
                                id="Sedes"
                                required
                                onChange={(e) => {
                                    onItemSedes(...itemsForm, e);
                                }}>
                                <option value="0">Selecione la sede</option>
                                <option value="Valledupar">Valledupar</option>
                                <option value="Aguachica">Aguachica</option>
                            </FormInput>
                            <Form.Control.Feedback>Looks good!</Form.Control.Feedback>
                        </Form.Group>
                        <Form.Group className={classnames("w-15 p-0")}>
                <Form.Group className="form-group mb-3">
                        <FormInput
                             type="select"
                             label="Ao"
                             name="fecha"
                             className="form-control"
                             containerClass={'w-65  p-0'}
                             key="fecha"
                             id="fecha"
                             required
                             onChange={(e) => {
                              onSelectFecha(...itemsForm, e);
                             }}>
                                <option value="0">Seleccione el Ao</option>
                                {fechas?.map((p, i) => {
                                    return (
                                        <option value={p} key={i + 1}>
                                            {p}
                                        </option>
                                    );
                                })}
                            </FormInput>
                      <Form.Control.Feedback type="invalid">
                        Please provide a valid Fecha Inicial.
                    </Form.Control.Feedback>
                </Form.Group>
            </Form.Group>
                   <Form.Group className={classnames('w-15 p-0')}>
                            <Form.Group className="form-group mb-3">
                                <Form.Control
                                    type="hidden"
                                    label="itemsmenu"
                                    value={itemsmenu}
                                    name="itemsmenu"
                                    key="itemsmenu"
                                    id="itemsmenu"
                                />
                                <Form.Control
                                    type="hidden"
                                    label="itemsmenuprincipal"
                                    value={itemsmenuprincipal}
                                    name="itemsmenuprincipal"
                                    key="itemsmenuprincipal"
                                    id="itemsmenuprincipal"
                                />
                                <Form.Control.Feedback type="invalid">
                                    Please provide a valid Final.
                                </Form.Control.Feedback>
                            </Form.Group>
                        </Form.Group>
                        <Form.Group className="w-15 p-1">
                            <div className="form-group mb-3">
                                <label className="form-label">{''}</label> <br />
                                {isLoading ? (
                                    <Button type="submit" variant="success">
                                        Consultar
                                    </Button>
                                ) : (
                                    <Button color="primary" disabled>
                                        <Spinner className="spinner-border-sm" tag="span" color="white" />
                                        <span className="visually-hidden">Loading...</span>
                                    </Button>
                                )}
                            </div>
                        </Form.Group>
                    </Form.Group>
                </Form>
            </Alert>
        </>
    );
};
export default memo(SearchFechas);
