import React, { useEffect, useState } from 'react';
import { Row, Col, Card } from 'react-bootstrap';
import ExcelToJson from '../components/ExcelToJsonComponent/ExcelToJsonComponent';
import Table from '../components/Table';
const HerramientaImportar = ({
    JsonDataSetter,
    jsonDataImport,
    setSelectedFile,
    selectedFile,
    importarDataCvs,
}) => {
    const [items, setEstados] = useState();
    const [validar, setValidar] = useState(true);
    const columns = [
        {
            Header: 'Programa',
            accessor: 'Programa',
            sort: false,
        },
        {
            Header: 'NombresApellidos',
            accessor: 'NombresApellidos',
            sort: false,
        },
        {
            Header: 'Identificacion',
            accessor: 'Identificacion',
            sort: false,
        },
        {
            Header: 'Fecha',
            accessor: 'Fecha',
            sort: false,
        },
        {
            Header: 'Periodo',
            accessor: 'Periodo',
            sort: false,
        },
        {
            Header: 'Sede',
            accessor: 'Sede',
            sort: false,
        },
    ];
    const sizePerPageList = [
        {
            text: '5',
            value: 5,
        },
        {
            text: '10',
            value: 10,
        },
        {
            text: '25',
            value: 25,
        },
        {
            text: 'All',
            value: 9999,
        },
    ];

    useEffect(() => {
        if (jsonDataImport) {
            const objet = Object.keys(jsonDataImport)?.map((key) => {
                return {
                    Programa: jsonDataImport[key]?.Programa ? jsonDataImport[key]?.Programa : setValidar(false),
                    NombresApellidos: jsonDataImport[key]?.NombresApellidos
                        ? jsonDataImport[key]?.NombresApellidos
                        : setValidar(false),
                    Identificacion: jsonDataImport[key]?.Identificacion
                        ? jsonDataImport[key]?.Identificacion
                        : setValidar(false),
                    Fecha: jsonDataImport[key]?.Fecha ? jsonDataImport[key]?.Fecha : setValidar(false),
                    Periodo: jsonDataImport[key]?.Periodo ? jsonDataImport[key]?.Periodo : setValidar(false),
                    Sede: jsonDataImport[key]?.Sede ? jsonDataImport[key]?.Sede : setValidar(false),
                };
            });
            setEstados(objet);
        }
    }, [jsonDataImport]);

    //console.log(validar,selectedFile)
    return (
        <React.Fragment>
            <Row>
                <Col>
                    <Card>
                        <Card.Body>
                            <ExcelToJson
                                JsonDataSetter={JsonDataSetter}
                                setSelectedFile={setSelectedFile}
                                selectedFile={selectedFile}
                                validar={validar}
                                importarDataCvs={importarDataCvs}
                            />
                        </Card.Body>
                    </Card>
                </Col>
            </Row>
            <Row>
                <Col sm="12 mt-1">
                    {items?.length > 0 && (
                        <Table
                            columns={columns}
                            data={items}
                            pageSize={5}
                            sizePerPageList={sizePerPageList}
                            isSortable={true}
                            pagination={true}
                            isSearchable={true}
                            isVisible={true}
                        />
                    )}
                </Col>
            </Row>
        </React.Fragment>
    );
};

export default HerramientaImportar;
