import React, { useContext } from 'react';
import Graduados from './Graduados';
import Matriculados from './Matriculados';
import Admitidos from './Admitidos';
import Inscritos from './Inscritos';
import TotalesFecha from './TotalesFecha';
import Title from '../../../pages/dashboard/components/Title';
import ListaUsuarios from '../components/Usuarios/ListaUsuarios';
import HerramientaImportar from './HerramientaImportar';
import { DashboardContext } from '../../../layouts/context/DashboardContext';


const ProjectDashboard = () => {
  const { itemsmenuprincipal } = useContext(DashboardContext)

  // eslint-disable-next-line no-undef

  return (
    <React.Fragment>
      <Title />
      {(() => {
        switch (itemsmenuprincipal) {
          case 'graduados':
            return (
              <>
                <Graduados />
              </>
            );
          case 'matriculados':
            return (
              <>
                <Matriculados />
              </>
            );
          case 'inscritos':
            return (<>
              <Inscritos />
            </>);
          case 'admitidos':
            return (<>
              <Admitidos />
            </>);
          case 'herramienta':
            return (<>
              <HerramientaImportar />
            </>);
          case 'configuracion':
            return (<>
              <ListaUsuarios /></>);
          case 'por_fecha':
            return (<>
              <TotalesFecha /></>);
          default:
            return (
              <>{''}</>
            );
        }
      })()}
    </React.Fragment>
  );
};
ProjectDashboard.defaultProps = {
  itemsmenu: '/',
};
export default ProjectDashboard;
