import React, { useContext } from 'react';
import { Row, Col, Tab, Card, Nav } from 'react-bootstrap';
import classnames from 'classnames';
import Table from '../../../components/Table';
//import Chart from 'react-apexcharts';
import SearchFechas from '../components/BuscadorProgramas/SearchFechas';
//import ApexBarChartData from '../estadisticas/ApexBarChartData';
import GraficaFecha from '../estadisticas/GraficaFecha';
//import LineChart from '../estadisticas/LineChart';
//import PieChart from '../estadisticas/PieChart';
//import MixedChart from '../estadisticas/MixedChart';
import { DashboardContext } from '../../../layouts/context/DashboardContext';
import { tabAdmitidos, columFecha} from './menu';
const TotalesFecha = () => {
  const { categoryPorFecha,itemUrl } = useContext(DashboardContext);
   return (
    <React.Fragment>
      <Row>
        <Card>
          <Card.Body>
            <Tab.Container defaultActiveKey="1">
              <Nav variant="tabs" className="nav-bordered" as="ul">
                {tabAdmitidos?.map((tab, index) => {
                  return (
                    <Nav.Item as="li" key={index}>
                      <Nav.Link href="#" eventKey={tab.id}>
                        <i className={classnames(tab.icon, 'd-md-none', 'd-block', 'me-1')}></i>
                        <span className="d-none d-md-block">{tab.title}</span>
                      </Nav.Link>
                    </Nav.Item>
                  );
                })}
              </Nav>
              <Tab.Content className="px-4 pb-4 pt-0 mx-auto">
                {tabAdmitidos?.map((tab, index) => {
                  return (
                    <Tab.Pane eventKey={tab.id} id={tab.id} key={index}>
                      <Row>
                        <Col sm="12 mt-1">
                          {(() => {
                            switch (tab.id) {
                              case 1:
                                return (
                                  <>
                                    <Row>
                                      <Col sm="12 mt-1">
                                        <SearchFechas />
                                      </Col>
                                    </Row>
                                    <Row>
                                      <Col sm="12 mt-1">
                                        {categoryPorFecha?.Items?.length >
                                          0 && (
                                            <Table
                                              columns={columFecha}
                                              data={categoryPorFecha?.Items}
                                              isSortable={true}
                                              isSearchable={true}
                                              numtable={itemUrl}
                                              isVisible={true}
                                            />
                                          )}
                                      </Col>
                                    </Row>
                                  </>
                                );
                              case 2:
                                return (
                                  <>
                                    <Row>
                                      <Col xl={6}>

                                      {categoryPorFecha?.Items?.length >
                                          0 && (
                                            <GraficaFecha
                                              barChartData={
                                                categoryPorFecha.Piechart
                                              }
                                            />
                                            )}
                                      </Col>

                                    </Row>
                                  </>
                                );
                              default:
                                return 'defould';
                            }
                          })()}
                        </Col>
                      </Row>
                    </Tab.Pane>
                  );
                })}
              </Tab.Content>
            </Tab.Container>
          </Card.Body>
        </Card>
      </Row>
    </React.Fragment>
  );
};

export default TotalesFecha;
